import logo from "./logo.svg";
import "./App.css";
import Navbar from "./components/Navbar";
import Landing from "./components/Landing";
import Personal from "./components/Personal";
import Habilidades from "./components/Habilidades";
import Proyectos from "./components/Proyectos";
import Footer from "./components/Footer";
import ProgressBar from "react-scroll-progress-bar";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Home from "./components/Home";
import Brickit from "./pages/Brickit";
import RecursosHumanos from "./pages/RecursosHumanos";
import Instagram from "./pages/Instagram";
import Inmobiliaria from "./pages/Inmobiliaria";

function App() {
  return (
    <div className="flex flex-col min-w-screen">
      <Router>
        <Navbar></Navbar>
        <Routes>
          <Route path="/portfolio/" exact element={<Home />} />
          <Route path="/portfolio/brickit/" exact element={<Brickit />} />
          <Route path="/portfolio/recursoshumanos/" exact element={<RecursosHumanos />} />
          <Route path="/portfolio/instagram/" exact element={<Instagram />} />
          <Route path="/portfolio/inmobiliaria/" exact element={<Inmobiliaria />} />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
